    }

    .padding-0-5-10 {
        padding: 15px 0 30px;
    }

    .style-form-inline {

        text-align: right;
    }

    .style-md-form {

        text-align: right;
    }

    .font-size-title-qd {
        font-size: 12px;
    }

    .font-size-title-km {
        font-size: 18px;
    }

    .padding-p {
        padding: 4px
    }

    .font-size-title-price {
        font-size: 13px;
    }

    .style-padd {
        padding: 10px;
    }

    .style-padd-10 {
        padding: 10px;
    }

    .style-padd-top-bottom-10 {
        padding: 10px 0 10px 0
    }

    .font-size-title {
        font-size: 16px;
    }

    .style-box-shadow {
        border-radius: 10px;
        background: #ffffff;
    }

    .style-back-do {
        background-color: #ffdbc4;
        color: #ff6e11;
        padding: 2px 10px 2px 10px;
        border-radius: 10px;
    }

    .item img {
        border-radius: 40px;
    }

    .style-down {
        width: 100px;
        height: auto;
    }

    .style-padd-mar-0 {
        padding: 0;
        margin: 0;
    }

}

.style-color-blue {
    color: #1d2dea;
}

.hotel-item p {
    text-align: left;
}

/* .hotel-item .img img {
    height: 255px;
} */

.hotel-item .img {
    display: block;
    overflow: hidden;

    height: auto;
}

.style-img {
    width: 200px;
    height: auto;
}

.style-slider {
    display: block;
    overflow: hidden;
    border-radius: 20px;
    margin: 0 auto 0px auto;
}

.style-slider img {
    width: 100%;
}

.stretch-card>.card {
    width: 100%;
    min-width: 100%
}

.owl-carousel .owl-wrapper-outer {
    display: block;
    overflow: hidden;
    border-radius: 20px;
    margin: 0 auto 0px auto;
}

body {
    background-color: #f9f9fa
}

.flex {
    -webkit-box-flex: 1;
    -ms-flex: 1 1 auto;
    flex: 1 1 auto
}

.style-radius {
    display: block;
    overflow: hidden;
    border-radius: 40px;
    margin: 0 auto 0px auto;
}
</style>



