    // updated() {
    //     this.getsSearchHotel(this.$keywordSearch);
    // },
    methods: {
        async getList() {
            axios.defaults.headers = {
                'deviceid': 'device_for_web'
            }
            let {
                data
            } = await axios.get('https://api.go2joy.vn/hotelapi/home/view/findLimitHotelInCollection?hotelCollectionSn=11&longitude=106.6813099&latitude=10.7642086&limit=50&offset=0');

            this.tableData = data;
        },
        handleFilter() {
            this.data.page = 1;
            this.getList();
        },
        searHotel() {
            return this.postList.filter(post => {
                return post.title.toLowerCase().includes(this.search.toLowerCase())
            })
        },

        // search key
        async getsSearchHotel(keywordSearch) {
            axios.defaults.headers = {
                'deviceid': 'e4c821cb904b4f0d668733d7f251c22b',
                'authorization': 'd684c48579c2f87b4466f90a7593eb4a'
            }
            // https://api.go2joy.vn/
            let result = await axios.post(
                `http://118.69.235.218:8080/hotelapi/user/view/searchHotelListNew`, {
                        "kara":false,
                        "name":false,
                        "address":false,
                        "hashTag":false,
                        "keyword": this.keywordSearch,
                        "offset":0,
                        "limit":20,
                        "oneForAll":true
                  
                }
            );

            // console.log('data search', result)
            this.dataSearch =  JSON.parse(result.data.otherInfo)
            // console.log('this.dataSearch', this.dataSearch)
           
        }

        // handleEdit(index, row) {
        //     console.log(index, row);
        // },
