<template>
<BaseDialog v-bind="$attrs" v-on="$listeners">
    <div class="dialog-title title-color text-align-left">Tìm kiếm</div>
    <div class="dialog-body text-align-left">

        <!---->
        <el-form ref="form" :model="form">

            <el-form-item class="style-dialog">
                <!-- <el-cascader @change="changeQH" :options="options"  placeholder="Chọn" :props="props" collapse-tags></el-cascader> -->
                <div class="col-6" id="item1">
                    <ul v-for=" (dsTinh,index) in dsTinh" :key="index">
                        <li @click="getDanhSachHuyen(dsTinh.value)"> <a>{{ dsTinh.label }}</a></li>
                    </ul>
                </div>
                <div class="col-6" id="item">
                    <ul v-for=" (dsHuyen,index) in dsHuyen" :key="index">

                        <li @click="filterHotel(dsHuyen.sntinh,dsHuyen.snhuyen)"> <a>{{ dsHuyen.label }}</a></li>

                    </ul>
                </div>
            </el-form-item>
        </el-form>
    </div>
    <div class="dialog-footer">
        <button class="btn  btn-light" type="button" @click="$emit('update:active', false)">
            Kết thúc
        </button>
        <button class="btn btn-color router-link-active" type="button" @click="$emit('update:active', false)">
            <router-link class="router-link-active" tag="a" :to="{ name: 'CategoryFilter', params: { sntinh: this.sntinh , snhuyen: this.snhuyen }}">Áp dụng</router-link>
        </button>
    </div>
</BaseDialog>
</template>

<script>
import axios from "axios"
import BaseDialog from "./BaseDialog.vue";
const cityOptions = ['Giảm giá đặc biệt', 'Khuyến mãi', 'Khách sạn mới', 'Khách sạn nổi bật', 'Ảnh 360'];
export default {
    name: "AlertDialog",
    components: {
        BaseDialog
    },
    props: {
        title: String,
        content: String,
        confirmText: {
            type: String,
            default: "Ok"
        }
    },
    data() {
        return {
            maxPrice: 300000,
            minPrice: 0,
            offset: 0,
            limit: 50,
            certifiedHotel: true,
            key: '',
            category: '',
            gia: '',
            value1: 0,
            props: {
                multiple: true
            },
            value: [],
            state1: '',
            snhuyen: '',
            sntinh: '',
            dsTinh: [],
            dsHuyen: [],
            checkboxGroup2: ['Giảm giá đặc biệt'],
            cities: cityOptions,
            value2: 0,
            form: {
                name: '',
                region: '',
                date1: '',
                date2: '',
                delivery: false,
                type: [],
                resource: '',
                desc: ''
            },

        };
    },
    created() {
        this.loadAddress();
    },
    updated() {
        $("#item li a").click(function() {
        $("#item li").removeClass('active');
            $(this).parent().addClass('active');
        });
        $("#item1 li a").click(function() {
        $("#item1 li").removeClass('active');
            $(this).parent().addClass('active');
        });
    },

    methods: {
        async loadAddress() {
            axios.defaults.headers = {
                'deviceid': 'device_for_web',
                'authorization': ''
            }

            let {
                data
            } = await axios.get(`https://api.go2joy.vn/hotelapi/area/view/findAllProvinceCity`)
            // ds Huyện
            this.dsTinh = data.map((x) => {
                return {
                    value: x.sn,
                    label: x.name,
                    children: [],
                };
            })
            // console.log('ds TỈnh', this.dsTinh)
        },
        async getDanhSachHuyen(sn) {
            axios.defaults.headers = {
                'deviceid': 'device_for_web',
                'authorization': ''
            }
            let provinceSn = sn
            const {
                data

            } = await axios.get(`https://api.go2joy.vn/hotelapi/area/view/findAllDistrictInProvince?provinceSn=${provinceSn}`)
            console.log('data huyen', data)
            this.dsHuyen = data.map((x) => {
                return {
                    sntinh: x.provinceSn,
                    label: x.name,
                    snhuyen: x.sn
                };
            })

        },
        async filterHotel(sntinh, snhuyen) {
            this.snhuyen = snhuyen
            this.sntinh = sntinh

            //  axios.defaults.headers = {
            //     'deviceid': 'device_for_web',
            //     'authorization': ''
            // }
            // const {
            //     data
            // } = await axios.get(`https://api.go2joy.vn/hotelapi/hotel/view/findLimitHotelListInFilter2?districtSn=${snhuyen}&offset=0&latitude=10.7642693&minPrice=0&limit=50&maxPrice=3000000&sort=0&provinceSn=${sntinh}&longitude=106.6813455`)
            // console.log('dataHotel',data)
            // let dataHotelFilter = data.hotelFormList
            // console.log('dataHotel Fillter', dataHotelFilter)
        },

        loadAll() {
            return [{
                    "value": "Khoảng cách",
                    "link": "#"
                },
                {
                    "value": "Giá tăng dần",
                    "link": "#"
                },
                {
                    "value": "Giá giảm dần ",
                    "link": "#"
                },
                {
                    "value": "Xếp hạng và Bình chọn",
                    "link": "#"
                },
                {
                    "value": "Hot nhất",
                    "link": "#"
                },
                {
                    "value": "Khuyến mãi",
                    "link": "#"
                },
                {
                    "value": "Khách sạn mới",
                    "link": "#"
                }
            ];
        },

        querySearchAsync(queryString, cb) {
            var links = this.links;
            var results = queryString ? links.filter(this.createFilter(queryString)) : links;

            clearTimeout(this.timeout);
            this.timeout = setTimeout(() => {
                cb(results);
            }, 3000 * Math.random());
        },
        createFilter(queryString) {
            return (link) => {
                return (link.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
            };
        },
        handleSelect(item) {
            console.log(item);
        },
        onSubmit() {
            console.log('Kết thúc!');
        },
        handleRemove(file) {
            console.log(file);
        },
        handlePictureCardPreview(file) {
            this.dialogImageUrl = file.url;
            this.dialogVisible = true;
        },
        handleDownload(file) {
            console.log(file);
        },
        changeLocale(locale) {
            i18n.locale = locale;
        },
        formatTooltip(val) {
            return val / 10;
        },

        // mounted() {
        //     this.links = this.loadAll();
        // }
    }

};
</script>

<style scoped>
.form-control {
    width: 100%;
    height: 30px;
    padding: 0;
}

.style-dialog .col-6 ul li {
    cursor: pointer;
    padding-left: 15px;

}

.active {
    color: red !important;
}

.style-dialog .col-6 ul li:hover {
    background-color: bisque;
}

.style-dialog .col-6 ul li a:active {
    background: #A9DB73;
    color: #333;
    border-bottom: 1px solid #fff;
}

.dialog-title {
    font-size: 24px;
    font-weight: 700;
    margin-bottom: 18px;
}

.router-link-active {
    color: #ffffff
}

.router-link-active:hover {
    text-decoration: none;
    color: #ffffff !important;
    background: #e45c03;
}

.dialog-footer {
    text-align: right;
    margin-top: 20px;
}

.dialog-footer button {
    padding: 5px 10px;
    font-size: 16px;
    cursor: pointer;
}

.text-align-left {
    text-align: left;
}

.style-dialog {
    max-height: 400px;
    overflow-y: scroll;
}

.btn-color {
    background-color: #ff6400;
    color: #ffffff;
}

@media only screen and (max-width: 480px) and (min-width: 320px) {
    .style-dialog {
        max-height: 250px;
        overflow-y: scroll;
    }
}
</style>



