<script>
import axios from 'axios'
import Header from '@/components/ComponentsPartial/Header'
import Footer from '@/components/ComponentsPartial/Footer'
import {
    Link
} from 'element-ui';
export default {
    path: '/search',
    name: "Search",
    components: {
        Header,
        Footer
    },

    data() {
        return {
            tableData: [],
            search: '',
            dataSearch: [],
            keywordSearch: '',
            hashTaskSearch: '',
            dataStart: []
        }
    },
    created() {
        this.getList();
    },
    updated() {
        if( this.keywordSearch != ''){
          setTimeout.call(this.getsSearchHotel(this.keywordSearch), 2.0*1000)
        } else {
            this.getList()
        }
        // this.getsSearchHotel(this.keywordSearch)
    },

    methods: {
        async getList() {
            axios.defaults.headers = {
                'deviceid': 'device_for_web'
            }
            let {
                data
            } = await axios.get('https://api.go2joy.vn/hotelapi/home/view/findLimitHotelInCollection?hotelCollectionSn=11&longitude=106.6813099&latitude=10.7642086&limit=50&offset=0');

            this.tableData = data;
        },
        handleFilter() {
            this.data.page = 1;
            this.getList();
        },
        searHotel() {
            return this.postList.filter(post => {
                return post.title.toLowerCase().includes(this.search.toLowerCase())
            })
        },
        getsSearchHotel(keywordSearch) {
            axios.defaults.headers = {
                'deviceid': 'device_for_web',
                'authorization': ''
            }
            // https://api.go2joy.vn/
            this.keywordSearch = keywordSearch
            console.log('keywordSearch', this.keywordSearch)

            let result = axios.post(
                `https://api.go2joy.vn/hotelapi/user/view/searchHotelListNew`, {
                "kara": false,
                "name": true,
                "address": false,
                "hashTag": false,
                "keyword": this.keywordSearch,
                "offset": 0,
                "limit": 20,
                "oneForAll": false

                }
            )
            result.then(response => {
                this.tableData = response.data.hotelFormList
            })

         return this.tableData
        },

        async searchHashtask(hashTask) {

            axios.defaults.headers = {
                'deviceid': 'device_for_web',
                'authorization': ''
            }
            this.hashTaskSearch = hashTask
            // this.hashTag = hashTag
            // console.log('name hashtask', this.hashTag)

            // https://api.go2joy.vn/
            console.log('hashTask', this.hashTaskSearch)
            let result = await axios.post(
                `https://api.go2joy.vn/hotelapi/user/view/searchHotelListNew`, {
                    "kara": false,
                    "name": false,
                    "address": false,
                    "hashTag": true,
                    "keyword": this.hashTaskSearch,
                    "offset": 0,
                    "limit": 20,
                    "oneForAll": true
                }
            );
            this.tableData = result.data.hotelFormList
            console.log('this.tableData', this.tableData)
        }
    },

};
</script>

