import Vue from "vue";
import Router from "vue-router";
import Vuex from "vuex";
import ElementUI from "element-ui";
import index from "@/components/TrangChu/index.vue";
import DetailHotel from "@/components/DetailHotel/DetailHotel.vue";
import Category from "@/components/Category/Category.vue";
import ListPromotion from "@/components/Promotion/ListPromotion.vue";
import PromotionDetail from "@/components/Promotion/PromotionDetail.vue";
import Search from "@/components/Search/Search.vue";
import SearchHotel from '@/components/SearchHotel/SearchHotel'
import Vision from "@/components/Vision/Vision.vue";
import About from "@/components/About/About.vue";
import Recruitment from "@/components/Recruitment/Recruitment.vue";
import Comment from "@/components/Comment/Comment.vue";
import Events from '@/components/Events/Events'
import KhuyenMai from '@/components/KhuyenMai/KhuyenMai'
import Maps from '@/components/Map/Maps'
import Error404 from "@/components/Error404/Error404.vue";
import DetailHotelListRoom from '@/components/DetailHotelListRoom/DetailHotelListRoom'
import CategoryFilter from '@/components/CategoryFilter/CategoryFilter'
Vue.use(ElementUI);
Vue.use(Router);
Vue.use(Vuex);
let router = new Router({
  routes: [
    {
      path: "/",
      name: "index",
      component: index,
      props: true
    },
    {
      path: "/detailhotel/:sn",
      name: "DetailHotel",
      component: DetailHotel,
      props: true
    },
    {
      path: "/detailhotellistroom/:snhotel/:snroom",
      name: "DetailHotelListRoom",
      component: DetailHotelListRoom,
      props: true
    },

    {
      path: "/listpromotion",
      name: "ListPromotion",
      component: ListPromotion,
      props: true
    },
    {
      path: "/promotionDetail/:sn/:action",
      name: "PromotionDetail",
      component: PromotionDetail,
      props: true
    },

    {
      path: "/about",
      name: "About",
      component: About
    },
    {
      path: "/Comment/:sn",
      name: "Comment",
      component: Comment,
      props: true
    },
    {
      path: "/vision",
      name: "Vision",
      component: Vision
    },
    {
      path: "/search",
      name: "Search",
      component: Search
    },
     {
      path: "/searchhotel/:keywordSearch",
      name: "SearchHotel",
      component: SearchHotel
    },
    {
      path: "/recruitment",
      name: "Recruitment",
      component: Recruitment
    },
    {
      path: "/category/:ds",
      name: "Category",
      component: Category,
      props: true
    },
    {
      path: "/categoryfilter/:sntinh/:snhuyen",
      name: "CategoryFilter",
      component: CategoryFilter,
      props: true
    },
    {
      path: "/events",
      name: "Events",
      component: Events,
      props: true
    },
    {
      path: "/khuyenmai",
      name: "KhuyenMai",
      component: KhuyenMai,
      props: true
    },
    {
      path: "/maps",
      name: "Maps",
      component: Maps,
      props: true
    },
    {
      path: "/error404",
      component: Error404,
      name: "",
      hidden: true
    },
    {
      path: "*",
      redirect: "/error404",
      hidden: true
    }
  ],
  scrollBehavior() {
    return { x: 0, y: 0 };
  }
});
router.beforeEach((to, from, next) => {
      $('.preloader').fadeIn().delay(1000).fadeOut('slow');
    // console.log('Route', to, from, next)
    next()
})
export default router;



